import React, { useState, useEffect } from 'react';
import HeaderUser from "../../components/HeadUser";
import { useParams } from 'react-router-dom';
import axios from 'axios';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import moment from 'moment';

function ManageHotelPackages() {
    const { ID_user } = useParams();
    const [showModal, setShowModal] = useState(false);
    const [hotelOffers, setHotelOffers] = useState([]);
    const [selectedOffer, setSelectedOffer] = useState(null);
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);

    useEffect(() => {
        const fetchHotelOffers = async () => {
            try {
                const response = await axios.get(`http://localhost:5000/check-hotel-offers?ID_user=${ID_user}`);
                setHotelOffers(response.data);
            } catch (error) {
                console.error('Error fetching hotel offers:', error);
            }
        };

        fetchHotelOffers();
    }, [ID_user]);

    const handleAcceptClick = (offer) => {
        setSelectedOffer(offer);
        setShowModal(true);
    };

    const handleCancelClick = async (offer) => {
        try {
            const response = await axios.post('http://localhost:5000/cancel-hotel-offers', { HDID: String(offer.HDID).split(',')[0] });
            console.log('Response:', response.data);
            setHotelOffers(hotelOffers.filter(o => o.HDID !== offer.HDID));
        } catch (error) {
            console.error('Error cancelling hotel offer:', error.response ? error.response.data : error.message);
        }
    };
    
    const handleCreate = async () => {
        if (selectedOffer && startDate && endDate) {
            const hdid = String(selectedOffer.HDID).split(',')[0];
            const dataToSend = {
                HDID: hdid,
                CDID: selectedOffer.CDID,
                ID_deals: selectedOffer.ID_deals,
                Deadline_package: moment(endDate).format('YYYY-MM-DD'),
                S_Deadline_package: moment(startDate).format('YYYY-MM-DD'),
            };
    
            console.log('Data to send:', dataToSend);
    
            try {
                const response = await axios.post('http://localhost:5000/confirm-hotel-offer', dataToSend);
                console.log('Response:', response.data);
                setShowModal(false);
                // Update state or refresh data if needed
            } catch (error) {
                console.error('Error confirming hotel offer:', error.response ? error.response.data : error.message);
            }
        } else {
            console.error('Missing required fields for creating package');
        }
    };
    

    return (
        <>
            <HeaderUser />
            <h5 className="text-5xl font-extrabold dark:text-white" style={{ marginTop: '100px', marginLeft: '50px' }}>Manage</h5>
            <p className="ms-2 font-semibold text-gray-500 dark:text-gray-400" style={{ marginLeft: '50px' }}>Hotel package</p>

            <div className="grid grid-cols-2 md:grid-cols-3 gap-4" style={{ width: '1100px', marginLeft: '200px', marginTop: '50px' }}>
                {hotelOffers.map(offer => (
                    <div key={offer.HDID}>
                        <div className="max-w-sm bg-white border border-gray-200 rounded-lg shadow dark:bg-gray-800 dark:border-gray-700">
                            <div className="p-5">
                                <a href={`/matallh/${offer.ID_hotel}`} >
                                <img className="w-full h-48 object-cover rounded-t-lg" src={offer.MinImg_Url_Hotel} ></img>
                                    <h5 className="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white">{offer.NameH}</h5>
                                    <p><strong>HDID:</strong> {offer.HDID}</p>
                                    <p><strong>CDID:</strong> {offer.CDID}</p>
                                    <p><strong>ประเภทโรงแรม:</strong> {offer.NameTH}</p>
                                    <p><strong>ประเภทห้อง:</strong> {offer.NameTR}</p>
                                    <p><strong>ประเภทวิว:</strong> {offer.NameTV}</p>
                                    <p><strong>จำนวนห้อง:</strong> {offer.Number_of_room}</p>
                                    <p><strong>ราคา:</strong> {offer.PriceH}</p>
                                    <p><strong>วันเริ่มต้น:</strong> {offer.S_datetimeHD}</p>
                                    <p><strong>วันสิ้นสุด:</strong> {offer.E_datetimeHD}</p>
                                </a>

                                <a
                                    onClick={() => handleCancelClick(offer)}
                                    style={{ marginTop: '20px' }}
                                    className="inline-flex items-center px-3 py-2 text-sm font-medium text-center text-white bg-red-700 rounded-lg hover:bg-red-800 focus:ring-4 focus:outline-none focus:ring-blue-300 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
                                >
                                    Cancel
                                </a>

                                <a onClick={() => handleAcceptClick(offer)}
                                    style={{ marginLeft: '150px' }}
                                    className="inline-flex items-center px-3 py-2 text-sm font-medium text-center text-white bg-blue-700 rounded-lg hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
                                >
                                    Accept
                                </a>
                            </div>
                        </div>
                    </div>
                ))}
            </div>

            {showModal && (
                <div id="default-modal" className="fixed inset-0 z-50 overflow-y-auto">
                    <div className="flex items-center justify-center min-h-screen px-4 text-center sm:block sm:p-0">
                        <div className="fixed inset-0 transition-opacity">
                            <div className="absolute inset-0 bg-gray-500 opacity-75"></div>
                        </div>
                        <span className="hidden sm:inline-block sm:align-middle sm:h-screen">&#8203;</span>
                        <div style={{ height: '500px' }}
                            className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:align-middle sm:max-w-lg sm:w-full">
                            <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                                <div className="sm:flex sm:items-start">
                                    <div className="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                                        <h3 className="text-lg leading-6 font-medium text-gray-900">
                                            Create package
                                        </h3>
                                        <div className="mt-2">
                                            <p className="text-sm text-gray-500">
                                                Do you want to create a package?
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div>

                                <h3 className="text-lg leading-6 font-medium text-gray-900" style={{ marginLeft: '40px' }}>
                                    Choose your time
                                </h3>

                                <div style={{ marginTop: '10px', display: 'flex', justifyContent: 'center' }}>
                                    <div className="relative">
                                        <DatePicker
                                            selected={startDate}
                                            onChange={date => setStartDate(date)}
                                            selectsStart
                                            startDate={startDate}
                                            endDate={endDate}
                                            dateFormat="dd/MM/yyyy"
                                            className="border rounded-md py-2 px-3 focus:outline-none focus:ring focus:border-blue-300"
                                        />
                                        <span className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                                            <svg className="h-5 w-5 text-gray-400" fill="currentColor" viewBox="0 0 20 20">
                                                <path fillRule="evenodd" d="M6 2a2 2 0 00-2 2v1H3a1 1 0 100 2h1v8a2 2 0 002 2h8a2 2 0 002-2V7h1a1 1 0 100-2h-1V4a2 2 0 00-2-2H6zm0 2h8v1H6V4zm0 3v8h8V7H6z" clipRule="evenodd" />
                                            </svg>
                                        </span>
                                    </div>
                                    <div className="relative" style={{ marginLeft:'10px' }}>
                                        <DatePicker
                                            selected={endDate}
                                            onChange={date => setEndDate(date)}
                                            selectsEnd
                                            startDate={startDate}
                                            endDate={endDate}
                                            minDate={startDate}
                                            dateFormat="dd/MM/yyyy"
                                            className="border rounded-md py-2 px-3 focus:outline-none focus:ring focus:border-blue-300"
                                        />
                                        <span className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                                            <svg className="h-5 w-5 text-gray-400" fill="currentColor" viewBox="0 0 20 20">
                                                <path fillRule="evenodd" d="M6 2a2 2 0 00-2 2v1H3a1 1 0 100 2h1v8a2 2 0 002 2h8a2 2 0 002-2V7h1a1 1 0 100-2h-1V4a2 2 0 00-2-2H6zm0 2h8v1H6V4zm0 3v8h8V7H6z" clipRule="evenodd" />
                                            </svg>
                                        </span>
                                    </div>
                                </div>
                            </div>

                            <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                                <button onClick={handleCreate}
                                    type="button"
                                    className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-green-600 text-base font-medium text-white hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 sm:ml-3 sm:w-auto sm:text-sm">
                                    Create
                                </button>
                                <button onClick={() => setShowModal(false)}
                                    type="button"
                                    className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:w-auto sm:text-sm">
                                    Cancel
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
}

export default ManageHotelPackages;
