/* eslint-disable no-unused-vars */
import { AppBar, Toolbar, Typography } from "@mui/material";
import { useNavigate } from "react-router-dom";
import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { jwtDecode } from 'jwt-decode'; // Import the default export 'jwtDecode'

const UserDropdown = ({ onLogout, userId }) => {
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const dropdownRef = useRef(null);
  const [userData, setUserData] = useState(null);
  const navigate = useNavigate();

  const handleToggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
  };

  const handleClickOutside = (event) => {
    if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
      setIsDropdownOpen(false);
    }
  };

  useEffect(() => {
    if (isDropdownOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    } else {
      document.removeEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isDropdownOpen]);

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
        navigate('/login'); // Redirect to login if no token is found
        return;
    }
    try {
        const decoded = jwtDecode(token); // Decode the token
        const userId = decoded.id; // Extract user ID from decoded token
        const fetchUserData = async () => {
            try {
                const response = await axios.get(`http://localhost:5000/users/${userId}`);
                setUserData(response.data);
            } catch (error) {
                console.error('Error fetching user data:', error);
            }
        };
        fetchUserData();
    } catch (error) {
        console.error('Error decoding token:', error);
        navigate('/login'); // Redirect to login page if decoding fails
    }
}, [navigate]);
  return (
    <div>
      {userData ? (
        <div className="relative" style={{ position: 'relative', marginLeft: '25px' }} ref={dropdownRef}>
          <img
            id="avatarButton"
            data-dropdown-toggle="userDropdown"
            data-dropdown-placement="bottom-start"
            className="w-19 h-19 rounded-full ring-2 ring-gray-300 dark:ring-gray-500 cursor-pointer"
            src={userData.img}
            width={50}
            height={50}
            alt="avatar"
            onClick={handleToggleDropdown}
          />
          {isDropdownOpen && (
            <div
              id="userDropdown"
              className="z-10 bg-white divide-y divide-gray-100 rounded-lg shadow w-44 dark:bg-gray-700 dark:divide-gray-600 
                absolute mt-2"
              style={{ right: 0, top: '100%', backgroundColor: 'black' }}
            >
              <div className="px-4 py-3 text-sm text-gray-200 dark:text-white">
                <div>{userData.FL_name}</div>
                <div className="font-medium truncate text-xs">{userData.Email}</div>
              </div>
              <ul className="py-2 text-sm text-gray-400 dark:text-gray-200" aria-labelledby="avatarButton">
                <li>
                  <a href={`/edituser/${userId}`}
                    className="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white"
                  >
                    แก้ไขโปรไฟล์
                  </a>
                </li>
              </ul>
              <div className="py-1">
                <a href="#"
                  className="block px-4 py-2 text-sm text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-600
                  dark:text-gray-200 dark:hover:text-white"
                  onClick={onLogout}
                >
                  ออกจากระบบ
                </a>
              </div>
            </div>
          )}
        </div>
      ) : (
        <p>  </p>
      )}
    </div>
  );
}

const HeaderUser = () => {
  const navigate = useNavigate();
  const [userId, setUserId] = useState(null);
  const [notificationCount, setNotificationCount] = useState(0);

  const navigateToLoginPage = () => {
    navigate("/");
  };

  const navigateToManagementPage = () => {
    navigate("/management_hotel");
  };

  const navigateToDelasPage = () => {
    navigate(`/delas/${userId}`);
  };

  const navigateToHomePage = () => {
    navigate(`/homeen`); // Change this to your home route
  };

  const navigateToHomeConPage = () => {
    navigate("/homeen_con");
  };

  const navigateToHomePackPage = () => {
    navigate("/homeen_p");
  };

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token) {
      try {
        const decoded = jwtDecode(token);
        setUserId(decoded.id);
  
        if (decoded.id) {  // ตรวจสอบว่า userId ไม่เป็น null
          const fetchNotifications = async () => {
            try {
              const response = await axios.get(`http://localhost:5000/notifications?ID_user=${decoded.id}`);
              const { hotelOffers, concertOffers } = response.data;
              setNotificationCount(hotelOffers.length + concertOffers.length);
            } catch (error) {
              console.error('Error fetching notifications:', error);
            }
          };
  
          fetchNotifications();
        }
      } catch (error) {
        console.error('Error decoding token:', error);
        navigate('/login');
      }
    } else {
      navigate('/login');
    }
  }, [navigate]);
  

  return (
    <>
      <AppBar position="fixed">
        <Toolbar sx={{ backgroundColor: "black", justifyContent: "space-between", height: 80 }}>
          <div style={{ display: "flex", alignItems: "center" }}>
            <button onClick={navigateToHomePackPage}
              type="button"
              className="relative inline-flex items-center px-5 py-2.5 text-10x1 font-medium text-center"
            >
              <span className="sr-only"></span>
              แพ็คเกจ
            </button>
            <button onClick={navigateToHomePage}
              type="button"
              className="relative inline-flex items-center px-5 py-2.5 text-10x1 font-medium text-center"
            >
              <span className="sr-only"></span>
              โรงแรม
            </button>
            <button onClick={navigateToHomeConPage}
              type="button"
              className="relative inline-flex items-center px-5 py-2.5 text-10x1 font-medium text-center"
            >
              <span className="sr-only"></span>
              คอนเสิร์ต
            </button>
          </div>
          <Typography onClick={navigateToHomePage}
            gutterBottom
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              fontWeight: "bold",
              color: "white",
              fontFamily: "Myriad Pro, sans-serif",
            }}
            variant="h4"
            marginTop={"15px"}
          >
            TEE<span style={{ color: "skyblue" }}>MI</span>
          </Typography>
          <div style={{ display: "flex", alignItems: "center" }}>
            <button 
              type="button"
              className="relative inline-flex items-center px-2 py-1 text-10x1 font-medium text-amber-500 text-center"
              onClick={navigateToManagementPage}
            >
              <span className="sr-only"></span>
              การจัดการ
            </button>

            <button
              type="button"
              className="relative inline-flex items-center px-5 py-2.5 text-10x1 font-medium text-center"
              onClick={navigateToDelasPage}
            >
              <span className="sr-only">Notifications</span>
              การแจ้งเตือน
              {notificationCount > 0 && (
                <div className="absolute inline-flex items-center justify-center w-6 h-6 text-xs font-bold text-white bg-red-500 border-2 border-white rounded-full -top-1 -end-1 dark:border-gray-900">
                  {notificationCount}
                </div>
              )}
            </button>
            <UserDropdown onLogout={navigateToLoginPage} userId={userId} />
          </div>
        </Toolbar>
      </AppBar>
    </>
  );
}

export default HeaderUser;
