import React, { useState, useEffect } from 'react';
import Header from "../../components/Head";
import axios from 'axios';
import { useParams } from 'react-router-dom';


function UAllH(){
    const { ID_hotel } = useParams();
    const hotelID = ID_hotel ? ID_hotel.split(',')[0] : null;
    const [images, setImages] = useState([]);
    const [hotelDetails, setHotelDetails] = useState(null);
    const [chanelHotel, setChanelHotel] = useState(null);
    const [typeHotel, setTypeHotel] = useState(null);
    const [typeRoom, setTypeRoom] = useState(null);
    const [typeView, setTypeView] = useState(null);
    const [roomStatus, setRoomStatus] = useState(null); // New state for room status
    const initialVisibleImages = 3;
    const [visibleImages, setVisibleImages] = useState(initialVisibleImages);
    const [isExpanded, setIsExpanded] = useState(false);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedImage, setSelectedImage] = useState(null);
    const [dropdownOpen, setDropdownOpen] = useState(false);

    useEffect(() => {
        const fetchImages = async () => {
            try {
                const response = await fetch(`http://localhost:5000/pictures/${hotelID}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                const combinedImages = [
                    ...data.hotelPictures.map(pic => pic.Img_Url_Hotel),
                    ...data.roomPictures.map(pic => pic.Img_Url_Room)
                ];
                setImages(combinedImages);
            } catch (error) {
                console.error('Error fetching images:', error);
            }
        };

        const fetchHotelDetails = async () => {
            try {
                const response = await fetch(`http://localhost:5000/detailhotels/${hotelID}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setHotelDetails(data);

                if (data && data.length > 0) {
                    fetchTypeHotel(data[0].Type_hotel);
                    fetchTypeRoom(data[0].Type_room);
                    fetchTypeView(data[0].Type_view);
                    fetchRoomStatus(data[0].Status_room); // Fetch room status
                }
            } catch (error) {
                console.error('Error fetching hotel details:', error);
            }
        };

        const fetchTypeHotel = async (typeHotelId) => {
            try {
                const response = await fetch(`http://localhost:5000/typehotel-h?Type_hotel=${typeHotelId}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setTypeHotel(data[0]);
            } catch (error) {
                console.error('Error fetching type hotel:', error);
            }
        };

        const fetchTypeRoom = async (typeRoomId) => {
            try {
                const response = await fetch(`http://localhost:5000/typeroom-h?Type_room=${typeRoomId}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setTypeRoom(data[0]);
            } catch (error) {
                console.error('Error fetching type room:', error);
            }
        };

        const fetchTypeView = async (typeViewId) => {
            try {
                const response = await fetch(`http://localhost:5000/typeview-h?Type_view=${typeViewId}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setTypeView(data[0]);
            } catch (error) {
                console.error('Error fetching type view:', error);
            }
        };

        const fetchChanelHotel = async () => {
            try {
                const response = await fetch(`http://localhost:5000/chanelhotel/${ID_hotel}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setChanelHotel(data);
            } catch (error) {
                console.error('Error fetching ChanelHotel data:', error);
            }
        };

        const fetchRoomStatus = async (statusRoom) => {
            try {
                const response = await fetch(`http://localhost:5000/roomStatus/${statusRoom}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setRoomStatus(data);
            } catch (error) {
                console.error('Error fetching room status:', error);
            }
        };

        fetchImages();
        fetchHotelDetails();
        fetchChanelHotel();
    }, [hotelID]);

    const toggleImages = () => {
        setVisibleImages(isExpanded ? initialVisibleImages : images.length);
        setIsExpanded(!isExpanded);
    };

    const openModal = (imageSrc) => {
        setSelectedImage(imageSrc);
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setIsModalOpen(false);
        setSelectedImage(null);
    };

    const toggleDropdown = () => {
        setDropdownOpen(!dropdownOpen);
    };
    return(
        <>
            <Header/>
            {hotelDetails && (
                <>
                    <h5 className="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white" style={{ marginTop:'120px' , marginLeft: '80px' }}>
                        {hotelDetails[0].NameH}
                    </h5>
                    <p style={{ marginLeft: '80px' }}>{hotelDetails[0].AddressH}</p>
                </>
            )}

            <div className="booking-gallery" style={{ width: '1400px', marginLeft: '65px' }}>
                {images.slice(0, visibleImages).map((src, index) => (
                    <div className="booking-gallery-item" key={index} onClick={() => openModal(src)}>
                        <img src={src} alt={`Gallery Image ${index + 1}`} className="booking-gallery-image" />
                    </div>
                ))}
            </div>

            {hotelDetails && (
                <>
                    <div className="inline-flex rounded-md shadow-sm" role="group" style={{ marginLeft: '80px', marginTop: '20px' }}>
                        <button type="button" onClick={toggleImages}
                            className="inline-flex items-center px-4 py-2 text-sm font-medium text-gray-900 bg-transparent border 
                                    border-gray-900 rounded-s-lg hover:bg-gray-900 hover:text-white focus:z-10 focus:ring-2 
                                    focus:ring-gray-500 focus:bg-gray-900 focus:text-white dark:border-white dark:text-white 
                                    dark:hover:text-white dark:hover:bg-gray-700 dark:focus:bg-gray-700">
                            {isExpanded ? 'Show Less' : 'More'}
                            <svg className="w-[18px] h-[18px] text-gray-800 dark:text-white"
                                style={{ marginLeft: "10px" }}
                                aria-hidden="true" xmlns="http://www.w3.org/2000/svg"
                                width="24" height="24" fill="none" viewBox="0 0 24 24">
                                <path stroke="currentColor" strokeLinejoin="round"
                                    strokeWidth="0.9" d="M4 5a1 1 0 0 1 1-1h4a1 1 0 0 1 1 1v2a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V5Zm16 14a1 1 0 0 1-1 1h-4a1 1 0 0 1-1-1v-2a1 1 0 0 1 1 1h4a1 1 0 0 1 1-1v2ZM4 13a1 1 0 0 1 1-1h4a1 1 0 0 1 1 1v6a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-6Zm16-2a1 1 0 0 1-1 1h-4a1 1 0 0 1-1-1V5a1 1 0 0 1 1-1h4a1 1 0 0 1 1 1v6Z"/>
                            </svg>
                        </button>
                    </div>
                    <div className="details-container">
                        <div className="details">
                            <div className="details-content">
                                <h5 className="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white">
                                    รายละเอียด
                                </h5>
                                <p><strong>ประเภทโรงแรม:</strong> {typeHotel ? typeHotel.NameTH : 'Loading...'}</p>
                                <p><strong>ประเภทห้อง:</strong> {typeRoom ? typeRoom.NameTR : 'Loading...'}</p>
                                <p><strong>ประเภทวิว:</strong> {typeView ? typeView.NameTV : 'Loading...'}</p>
                                <p><strong>ราคา:</strong> {hotelDetails[0].PriceH}</p>
                                <p><strong>สถานะ:</strong> {roomStatus ? roomStatus[0].NameRS : 'Loading...'}</p> {/* Display room status */}
                                <p className="text-gray-900 dark:text-white">{hotelDetails[0].DetailH}</p>
                            </div>
                        </div>
                    </div>

                    <div className="details-container" style={{ marginRight:'1175px' }}>
                        <div className="details">
                            <div className="details-content">
                                <h5 className="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white">
                                    ช่องทางการจอง
                                </h5>
                                {chanelHotel && chanelHotel.map((item, index) => (
                                    <p key={index}><strong>Link:</strong> <a href={item.UrlH} target="_blank" rel="noopener noreferrer">กดตรงนี้</a></p>
                                ))}
                            </div>
                        </div>
                    </div>
                </>
            )}

            {isModalOpen && (
                <div className="modal" onClick={closeModal}>
                    <div className="modal-content">
                        <img src={selectedImage} alt="Modal" className="modal-image" />
                        <button className="close-button" onClick={closeModal}>&times;</button>
                    </div>
                </div>
            )}
        </>
    )
}

export default UAllH;