import React, { useState, useEffect } from 'react';
import HeaderUser from "../../components/HeadUser";
import { useParams, useNavigate } from 'react-router-dom';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { format } from 'date-fns';

function HotelDeals() {
    const { ID_hotel } = useParams();
    const navigate = useNavigate();
    const [numberOfTickets, setNumberOfTickets] = useState('');
    const [selectedRoomId, setSelectedRoomId] = useState('');
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);
    const [rooms, setRooms] = useState([]);

    const hotelID = ID_hotel ? ID_hotel.split(',')[0] : null;

    useEffect(() => {
        const fetchRoomData = async () => {
            try {
                const response = await fetch(`http://localhost:5000/select-room/${hotelID}`);
                if (response.ok) {
                    const data = await response.json();
                    setRooms(data);
                } else {
                    console.error('Failed to fetch room data');
                }
            } catch (error) {
                console.error('Error fetching room data:', error);
            }
        };

        if (hotelID) {
            fetchRoomData();
        }
    }, [hotelID]);

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!selectedRoomId || !numberOfTickets || !startDate || !endDate) {
            alert('Please fill in all fields before submitting.');
            return;
        }

        const formattedStartDate = format(startDate, 'yyyy-MM-dd');
        const formattedEndDate = format(endDate, 'yyyy-MM-dd');

        const data = {
            ID_room: selectedRoomId,
            Number_of_room: numberOfTickets,
            S_datetimeHD: formattedStartDate,
            E_datetimeHD: formattedEndDate,
            StatusHD: 1
        };

        try {
            const response = await fetch('http://localhost:5000/hoteldeals', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            });

            if (response.ok) {
                alert('Insert successful');
                navigate('/management_hotel');
            } else {
                alert('Failed to insert');
            }
        } catch (error) {
            console.error('Error submitting form:', error);
            alert('Error submitting form');
        }
    };

    return (
        <>
            <HeaderUser />
            <h5 className="text-5xl font-extrabold dark:text-white"
                style={{ marginTop: '100px', marginLeft: '50px' }}>Create</h5>
            <p className="ms-2 font-semibold text-gray-500 dark:text-gray-400"
                style={{ marginLeft: '50px' }}>Hotel Deals</p>

            <form className="max-w-sm mx-auto" onSubmit={handleSubmit}>
                <label htmlFor="roomSelect" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
                    Select Room
                </label>
                <select
                    id="roomSelect"
                    className="bg-gray-50 border border-gray-300 text-gray-900 mb-6 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                    value={selectedRoomId}
                    onChange={(e) => setSelectedRoomId(e.target.value)}
                >
                    <option value="" disabled>Select a room</option>
                    {rooms.map((room) => (
                        <option key={room.ID_room} value={room.ID_room}>
                            {`${room.NameTR} - ${room.NameTV}`}
                        </option>
                    ))}
                </select>

                <div className="mb-4">
                    <label htmlFor="Number" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
                        Number of Room
                    </label>
                    <input
                        type="number"
                        id="Number"
                        min="1"
                        value={numberOfTickets}
                        onChange={(e) => setNumberOfTickets(e.target.value)}
                        className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                        required
                    />
                </div>

                <label htmlFor="Type_hotel" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">
                    Start time , end time
                </label>
                <div id="date-range-picker" className="flex items-center mb-6">
                    <div className="relative">
                        <DatePicker
                            selected={startDate}
                            onChange={setStartDate}
                            selectsStart
                            startDate={startDate}
                            endDate={endDate}
                            className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 
                                focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 
                                dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                            placeholderText="Select start date"
                            dateFormat="dd/MM/yyyy"
                            showTimeSelect={false}
                        />
                    </div>
                    <span className="mx-4 text-gray-500">to</span>
                    <div className="relative">
                        <DatePicker
                            selected={endDate}
                            onChange={setEndDate}
                            selectsEnd
                            startDate={startDate}
                            endDate={endDate}
                            className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 
                                focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 
                                dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                            placeholderText="Select end date"
                            dateFormat="dd/MM/yyyy"
                            showTimeSelect={false}
                            minDate={startDate}
                        />
                    </div>
                </div>

                <button
                    type="submit"
                    className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
                >
                    Submit
                </button>
            </form>
        </>
    )
}

export default HotelDeals;
