import React, { useState, useEffect } from 'react';
import Header from "../../components/Head";
import { useParams, useNavigate } from 'react-router-dom';

function UMatAllH() {
    const { ID_hotel } = useParams();
    const [images, setImages] = useState([]);
    const [hotelDetails, setHotelDetails] = useState([]);
    const [chanelHotel, setChanelHotel] = useState([]);
    const [typeHotel, setTypeHotel] = useState(null);
    const [roomDetails, setRoomDetails] = useState([]);
    const initialVisibleImages = 6;
    const [visibleImages, setVisibleImages] = useState(initialVisibleImages);
    const [isExpanded, setIsExpanded] = useState(false);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedImage, setSelectedImage] = useState(null);
    const [roomImages, setRoomImages] = useState([]); // State for room images

    useEffect(() => {
        const fetchImages = async () => {
            try {
                const response = await fetch(`http://localhost:5000/pictures/${ID_hotel}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                const combinedImages = [
                    ...data.hotelPictures.map(pic => pic.Img_Url_Hotel),
                    ...data.roomPictures.map(pic => pic.Img_Url_Room)
                ];
                setImages(combinedImages);
            } catch (error) {
                console.error('Error fetching images:', error);
            }
        };

        const fetchHotelDetails = async () => {
            try {
                const response = await fetch(`http://localhost:5000/detailhotels/${ID_hotel}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setHotelDetails(data);

                if (data && data.length > 0) {
                    fetchTypeHotel(data[0].Type_hotel);
                }
            } catch (error) {
                console.error('Error fetching hotel details:', error);
            }
        };

        const fetchTypeHotel = async (typeHotelId) => {
            try {
                const response = await fetch(`http://localhost:5000/typehotel-h?Type_hotel=${typeHotelId}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setTypeHotel(data[0]);
            } catch (error) {
                console.error('Error fetching type hotel:', error);
            }
        };

        const fetchChanelHotel = async () => {
            try {
                const response = await fetch(`http://localhost:5000/chanelhotel/${ID_hotel}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setChanelHotel(data);
            } catch (error) {
                console.error('Error fetching ChanelHotel data:', error);
            }
        };

        const fetchRoomDetails = async () => {
            try {
                const response = await fetch(`http://localhost:5000/detail-room?ID_hotel=${ID_hotel}`);
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setRoomDetails(data);
            } catch (error) {
                console.error('Error fetching room details:', error);
            }
        };

        fetchImages();
        fetchHotelDetails();
        fetchChanelHotel();
        fetchRoomDetails();
    }, [ID_hotel]);

    const toggleImages = () => {
        setVisibleImages(isExpanded ? initialVisibleImages : images.length);
        setIsExpanded(!isExpanded);
    };

    const openModal = (imageSrc) => {
        setSelectedImage(imageSrc);
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setIsModalOpen(false);
        setSelectedImage(null);
    };

    const fetchRoomImages = async (ID_room) => {
        try {
            const response = await fetch(`http://localhost:5000/view-images?ID_room=${ID_room}`);
            if (!response.ok) {
                throw new Error('Network response was not ok');
            }
            const data = await response.json();
            setRoomImages(data.map(pic => pic.Img_Url_room));
            setIsModalOpen(true);
        } catch (error) {
            console.error('Error fetching room images:', error);
        }
    };


    return (
        <>
            <Header />
            <div className="gallery-container" style={{ marginTop: '120px' }}>
                
                <h5 className="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white" style={{ marginLeft: '80px' }}>
                    {hotelDetails.length > 0 && hotelDetails[0].NameH}
                </h5>
                <p style={{ marginLeft: '80px' }}>{hotelDetails.length > 0 && hotelDetails[0].AddressH}</p>
            </div>

            <div className="booking-gallery">
                {images.slice(0, visibleImages).map((src, index) => (
                    <div className="booking-img-container" key={index}>
                        <img
                            className="booking-img"
                            src={src}
                            alt={`Image ${index + 1}`}
                            onClick={() => openModal(src)}
                        />
                    </div>
                ))}
            </div>

            {images.length > initialVisibleImages && (
                <button
                    className="text-blue-600 hover:underline"
                    onClick={toggleImages}
                    style={{ marginLeft: '80px' }}
                >
                    {isExpanded ? 'See Less' : 'See More'}
                </button>
            )}

            {isModalOpen && (
                <div className="modal-overlay" onClick={closeModal}>
                    <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                        {selectedImage && <img src={selectedImage} alt="Selected" />}
                        {!selectedImage && roomImages.length > 0 && roomImages.map((src, index) => (
                            <img key={index} src={src} alt={`Room Image ${index + 1}`} />
                        ))}
                        <button className="close-button" onClick={closeModal} style={{ marginRight: '20px' }}>
                            Close
                        </button>
                    </div>
                </div>
            )}

            {hotelDetails.length > 0 && (
                <div className="hotel-details" style={{ marginLeft: '80px', marginTop: '20px', width: '1300px' }}>
                    <h3 className="text-lg font-bold mb-2">{typeHotel && <p><strong></strong>{typeHotel.NameTH}</p>}</h3>
                    <p><strong></strong>{hotelDetails[0].DetailH}</p>
                </div>
            )}

            {roomDetails.length > 0 && (
                <div className="room-details" style={{ marginLeft: '80px', marginTop: '20px', width: '1300px' }}>
                    <h3 className="text-lg font-bold mb-2">Room Details</h3>
                    <table className="table-auto w-full text-left">
                        <thead>
                            <tr>
                                <th className="px-4 py-2">Type of Room</th>
                                <th className="px-4 py-2">Type of View</th>
                                <th className="px-4 py-2">Status</th>
                                <th className="px-4 py-2">Price</th>
                                <th className="px-4 py-2">Images</th>
                            </tr>
                        </thead>
                        <tbody>
                            {roomDetails.map((room, index) => (
                                <tr 
                                    key={index} 
                                    className="hover:bg-gray-200 cursor-pointer"
                                >
                                    <td className="border px-4 py-2">{room.NameTR}</td>
                                    <td className="border px-4 py-2">{room.NameTV}</td>
                                    <td className="border px-4 py-2">{room.NameRS}</td>
                                    <td className="border px-4 py-2">{room.PriceH} $</td>
                                    <td className="border px-4 py-2">
                                        <button
                                            onClick={(e) => {
                                                e.stopPropagation();
                                                fetchRoomImages(room.ID_room);
                                            }}
                                            className="text-blue-600 hover:underline"
                                        >
                                            View Images
                                        </button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            )}
            <div className="details-container" style={{ marginRight:'1175px' }}>
                        <div className="details">
                            <div className="details-content">
                                <h5 className="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white">
                                    ช่องทางการจอง
                                </h5>
                                {chanelHotel && chanelHotel.map((item, index) => (
                                    <p key={index}><strong>Link:</strong> <a href={item.UrlH} target="_blank" rel="noopener noreferrer">กดตรงนี้</a></p>
                                ))}
                            </div>
                        </div>
                    </div>
        </>
    );
}

export default UMatAllH;
