import React, { useState, useEffect } from 'react';
import HeaderUser from "../../components/HeadUser";
import { useParams } from 'react-router-dom';
import axios from 'axios';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

function CMH() {
    const { ID_user, ID_hotel, HDID } = useParams();
    const [concertDeals, setConcertDeals] = useState([]);
    const [showModal, setShowModal] = useState(false);
    const [selectedDeal, setSelectedDeal] = useState(null);
    const [statusDescriptions, setStatusDescriptions] = useState({});
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);

    useEffect(() => {
        const fetchConcertDeals = async () => {
            try {
                const response = await fetch(`http://localhost:5000/concertsdeals-you-status/1/${ID_user}`);
                const deals = await response.json();
                setConcertDeals(deals);
            } catch (error) {
                console.error("Error fetching the concert deals:", error);
            }
        };

        fetchConcertDeals();
    }, [ID_user]);

    useEffect(() => {
        if (concertDeals.length > 0) {
            const fetchStatusDescriptions = async () => {
                try {
                    const statusPromises = concertDeals.map(deal =>
                        fetch(`http://localhost:5000/offerStatus-c/${deal.StatusCD}`).then(res => res.json())
                    );

                    const statuses = await Promise.all(statusPromises);
                    const statusMap = concertDeals.reduce((acc, deal, index) => {
                        const nameOS = statuses[index][0].NameOS;
                        console.log(`Deal StatusCD: ${deal.StatusCD}, NameOS: ${nameOS}`);
                        acc[deal.StatusCD] = nameOS;
                        return acc;
                    }, {});

                    setStatusDescriptions(statusMap);
                } catch (error) {
                    console.error("Error fetching the status descriptions:", error);
                }
            };

            fetchStatusDescriptions();
        }
    }, [concertDeals]);

    const handleAddDeal = async () => {
        if (!endDate || !selectedDeal) {
            alert("Please select a date and a deal.");
            return;
        }

        const data = {
            HDID: HDID,
            CDID: selectedDeal,
            Datetime_match: endDate.toISOString().split('T')[0],
            StatusD: 2,
            Deadline_package: endDate.toISOString().split('T')[0], // assuming deadline package is the same as end date
            S_Deadline_package: startDate.toISOString().split('T')[0] // assuming start deadline package is the start date
        };

        try {
            const response = await axios.post('http://localhost:5000/add-deals-update-insert-cdid', data);
            if (response.status === 201) {
                alert('Deal added successfully');
                setShowModal(false);
            } else {
                alert('Failed to add deal');
            }
        } catch (error) {
            console.error('Error adding deal:', error);
            alert('An error occurred while adding the deal.');
        }
    };

    return (
        <>
            <HeaderUser />
            <h1 className="text-5xl font-extrabold dark:text-white" style={{ marginTop: '100px', marginLeft: '50px' }}>
                Matching 
            </h1>
            <p className="ms-2 font-semibold text-gray-500 dark:text-gray-400" style={{ marginLeft: '50px' }}>hotels with concerts.</p>
            <div className="grid grid-cols-2 md:grid-cols-3 gap-4" style={{ width: '1300px', marginLeft: '120px', marginTop: '80px' }}>
                {concertDeals.map(deal => (
                    <div key={deal.DealID} className="flex flex-col max-w-sm bg-white border border-gray-200 rounded-lg shadow dark:bg-gray-800 dark:border-gray-700" 
                        style={{ height: '800px' }}>
                        <a href={`/matallc/${deal.CID}`} style={{ flexGrow: 1, textDecoration: 'none' }}>
                            <div style={{ height: '55%' }}>
                                <img className="rounded-t-lg" style={{ height: '80%', display: 'block', margin: '0 auto', marginTop:'25px' }} src={deal.Poster} alt="" />
                            </div>
                            <div className="p-5">
                                <h5 className="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white" style={{ marginTop:'-70px' }}>
                                    {deal.Name}
                                </h5>
                                <p><strong>ID:</strong> {deal.CDID}</p>
                                <p><strong>จำนวนบัตร:</strong> {deal.Number_of_ticket}</p>
                                <p><strong>ราคา:</strong> {deal.PriceCD}</p>
                                <p><strong>วันเริ่มต้น:</strong> {deal.S_datetime}</p>
                                <p><strong>วันสิ้นสุด:</strong> {deal.E_datetime}</p>
                                <p><strong>สถานะ:</strong> {statusDescriptions[deal.StatusCD]}</p>
                            </div>
                        </a>
                        <div className="p-5" style={{ padding: '0 1rem 1rem 1rem' }}>
                            <button
                                onClick={() => {
                                    setSelectedDeal(deal.CDID);
                                    setShowModal(true);
                                }}
                                className="inline-flex items-center px-3 py-2 text-sm font-medium text-center text-white bg-blue-700 rounded-lg hover:bg-blue-800 
                                            focus:ring-4 focus:outline-none focus:ring-blue-300 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800 
                                            w-full justify-center"
                            >
                                Send
                            </button>
                        </div>
                    </div>
                ))}
            </div>
            {showModal && selectedDeal && (
                <div id="default-modal" className="fixed inset-0 z-50 overflow-y-auto" >
                    <div className="flex items-center justify-center min-h-screen px-4 text-center sm:block sm:p-0" >
                        <div className="fixed inset-0 transition-opacity">
                            <div className="absolute inset-0 bg-gray-500 opacity-75"></div>
                        </div>
                        <span className="hidden sm:inline-block sm:align-middle sm:h-screen">&#8203;</span>
                        <div style={{ height: '500px' }}
                                className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:align-middle sm:max-w-lg sm:w-full">
                            <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                                <div className="sm:flex sm:items-start">
                                    <div className="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                                        <h3 className="text-lg leading-6 font-medium text-gray-900">
                                            Deals
                                        </h3>
                                        <div className="mt-2">
                                            <p className="text-sm text-gray-500">
                                                Want to submit a proposal?
                                            </p>
                                        </div>
                                        <div className="mt-2">
                                            <p className="text-sm text-gray-500">
                                                End date set 
                                            </p>
                                        </div>
                                        <div className="relative max-w-sm">
                                            <div className="flex justify-between px-4">
                                                <label className="block text-gray-700 font-bold mb-2">Start Date:</label>
                                                <DatePicker
                                                    selected={startDate}
                                                    onChange={(date) => setStartDate(date)}
                                                    dateFormat="dd/MM/yyyy"
                                                    className="form-input mt-1 block w-full"
                                                />
                                            </div>
                                            <div className="flex justify-between px-4">
                                                <label className="block text-gray-700 font-bold mb-2">End Date:</label>
                                                <DatePicker
                                                    selected={endDate}
                                                    onChange={(date) => setEndDate(date)}
                                                    dateFormat="dd/MM/yyyy"
                                                    className="form-input mt-1 block w-full"
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="bg-gray-50 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6" 
                                    style={{marginTop:'200px'}}>
                                <button
                                    type="button"
                                    onClick={handleAddDeal}
                                    className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-green-600 text-base font-medium text-white hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm"
                                >
                                    Yes
                                </button>
                                <button
                                    onClick={() => setShowModal(false)}
                                    type="button"
                                    className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:w-auto sm:text-sm"
                                >
                                    Cancel
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </>
    )
}

export default CMH;
